import { Component, OnInit , ViewChild} from '@angular/core';
import { ModalController , NavParams} from '@ionic/angular';
import { FormGroup, FormControl, Validators, FormGroupDirective } from '@angular/forms';
import { AuthenticationService } from '../../../services/Authentication.service';
import { TranslateService } from '@ngx-translate/core';
@Component({
  selector: 'app-modulo-create',
  templateUrl: './modulo-create.page.html',
  styleUrls: ['./modulo-create.page.scss'],
})
export class ModuloCreatePage implements OnInit {

  Form: FormGroup;
  @ViewChild('createForm', { static: false }) createForm: FormGroupDirective;
  variables: any = [];
  userinfo: any = [];
  tituloxx = '';
  vgrupo: any = [];

  _ID: any;
  lpermiso: any;


  constructor(
    private modalController: ModalController,
    public translateService: TranslateService    , private authenticationService: AuthenticationService,
    private navParams: NavParams
  ) { }

  dismissModal() {
    this.modalController.dismiss('close');
  }
  ionViewDidEnter(): void {

  }
  ngOnInit(): void {
    this.variables = this.navParams.get('variables');
    this.userinfo = this.navParams.get('userinfo');
    this.tituloxx = this.variables.nombre;
    this.Form = new FormGroup({
      'nombre': new FormControl('', Validators.required),
      'grupo': new FormControl('', Validators.required),
      'titulo': new FormControl('', Validators.required),
      'index': new FormControl('', Validators.required),
      'icono': new FormControl('', Validators.required),
      'nivel': new FormControl('', Validators.required),
      'estado': new FormControl('', Validators.required)
    });


    if (this.navParams.get('record') == null) {
      this.authenticationService.getReg('modulo-grupo', 'datosfijos', 0).then((result) => {
        this.vgrupo = result;
      }, (err) => {
        this.authenticationService.presentAlert(err.error , 'Precaución' , '');
    });
    this.Form.controls['index'].setValue(10);
      this.translateService.get('NUEVOM').subscribe((value) => {   this.tituloxx = value +' '+ this.variables.nombre;this.lpermiso = value; });
      this.authenticationService.getReg('todos/orden/' + this.userinfo[0].idempresa._id, 'modulos', 0).then((data) => {
        if (data) {
          const aa: any = data;
          setTimeout( () => { this.Form.controls['index'].setValue(Number(aa.orden) + 10); });


          
        }
  }, (err) => {
    this.authenticationService.presentAlert(err.error , 'Precaución' , '');
  });
    } else {
      this.translateService.get('ACTUALIZAM').subscribe((value) => {this.tituloxx = value + ' '+ this.variables.nombre;
         this.lpermiso = value; });

      this._ID 				=	this.navParams.data.record._id;
      this.Form.controls['nombre'].setValue(this.navParams.data.record.nombre);
        this.authenticationService.getReg('modulo-grupo', 'datosfijos', 0).then((result) => {
            this.vgrupo = result;
            setTimeout( () => {   this.Form.controls['grupo'].setValue(this.navParams.data.record.grupo); });
            
      

           
        }, (err) => {
        this.authenticationService.presentAlert(err.error , 'Precaución' , '');
        });
    //  this.grupo  = {id:'PAGINA',nombre:'PAGINA'} ;
      this.Form.controls['titulo'].setValue(this.navParams.data.record.titulo);
      this.Form.controls['index'].setValue(this.navParams.data.record.index);
      this.Form.controls['icono'].setValue(this.navParams.data.record.icono);
      this.Form.controls['nivel'].setValue(this.navParams.data.record.nivel);
      this.Form.controls['estado'].setValue(this.navParams.data.record.estado);
    }
  }

  //onSgrupoChange(selectedValue: any) {    this.sgrupo = selectedValue.detail.value ;(ionChange)="onSgrupoChange($event)"   }

  submitForm() {

    const   options: any	     = {
      idempresa: this.userinfo[0].idempresa._id,
    nombre : this.Form.controls['nombre'].value,
    grupo: this.Form.controls['grupo'].value,
    titulo: this.Form.controls['titulo'].value,
    componente: this.Form.controls['titulo'].value,
    tabcomponente: this.Form.controls['titulo'].value,
    index: this.Form.controls['index'].value,
    icono: this.Form.controls['icono'].value,
    estado : this.Form.controls['estado'].value,
    nivel : this.Form.controls['nivel'].value,
    bitacora: { idempresa : this.userinfo[0].idempresa._id , idafiliado: '' ,
    email: this.userinfo[0].username , permiso : this.lpermiso, accion: this.tituloxx}
   };

   if (this.navParams.get('record') == null) {
        if (options) {
          this.authenticationService.createReg('', options, 'modulos').then((result) => {
            if (!this.Form.valid) { return; }
            this.modalController.dismiss('ok');
        }, (err) => {
          this.authenticationService.presentAlert(err.error , 'Precaución' , '');
        });
        }
   } else {
    if (options) {
      this.authenticationService.createReg(this._ID, options, 'modulos').then((result) => {
         if (!this.Form.valid) { return; }
         this.modalController.dismiss('ok');
     }, (err) => {
      this.authenticationService.presentAlert(err.error , 'Precaución' , '');
     });
  }}




  }



}
